html, body {
    margin: 0px;
    padding: 0px;
    display: flex;
    justify-content: center;
    font-family: system-ui, Avenir, Helvetica, Arial, sans-serif;
    place-items: top;
    min-width: 320px;
    min-height: 100vh;
    line-height: 1.5;
    font-weight: 400;
    text-align: center;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

p {
  font-size: 18px;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
  cursor: pointer;
}

a:hover {
  color: #535bf2;
}

.flex-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media screen and (max-width: 420px) {
  .app-size {
    width: 100%;
    width: 100vw;
    margin: 15px 15px;
  }
}

@media screen and (min-width: 421px) and (max-width: 768px) {
  .app-size {
    width: 95%;
    width: 95vw;
    margin: 15px 15px;
  }
}

@media screen and (max-width: 768px) {
  .flex-container {
    flex-wrap: wrap;
  }

  .category-difficulty-name {
    width: 100%;
    text-align: center;
  }

  .category-difficulty-pill {
    width: 100%;
    text-align: center;
  }
}

@media screen and (min-width: 769px) {
  .app-size {
    width: 750px;
  }

  .category-difficulty-name {
    width: 100%;
    text-align: left;
  }

  .category-difficulty-pill {
    width: 100%;
    text-align: right;
  }
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  cursor: pointer;
  transition: border-color 0.25s;
  border-color: white;
}

button:hover {
  border-color: black;
}

button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

button:disabled {
  color: lightgray;
  border-color: white;
  cursor: auto;
}

.button-ok {
  background-color:dodgerblue;
  color: white;
}
